








































import {Component, Vue} from 'vue-property-decorator';
import DatTitle from '@/components/DatTitle.vue';
import {RsvpRecord} from '@/helpers/Classes';

@Component({
  components: {DatTitle},
})

export default class RSVPList extends Vue {
  public rsvps: RsvpRecord[] = [];
  public showError: boolean = false;

  public mounted() {
    fetch('//datwedding2020.herokuapp.com/rsvps').then((response) => {
      response.json().then((list) => {
        this.rsvps = list.rsvps as RsvpRecord[];
      });
    }).catch(() => {
      this.showError = true;
    });
  }
}
